
import $ from 'jquery'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'




let isAndroid = 0
let browser = {
versions: () => {
let u = navigator.userAgent
return {
mobile: !!u.match(/AppleWebKit.*Mobile.*/), // 是否为移动终端
ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端
android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 
}
}
}
browser.versions()

export default {
directives: {
TransferDom
},
components: {
'lt-button': require('../components/lt-button').default,
banner1: require('./banner1-pc').default,
banner2: require('./banner2').default,
banner3: require('./banner3').default,
XImg,
XHeader,
Swiper,
SwiperItem,
Popup,
Flexbox,
FlexboxItem,
Clocker,
XDialog
},
data () {
return {
firstVisibility: false,

drawerVisibility: false,
bannerList: [
'static/pchome/banner-1.png'



],
bannerIndex: 0,

videoList: [
'static/pchome/video-kunao.png'


],
videos: [
'video-kunao.mp4',
'video-xizao.mp4',
'video-zhuomicang.mp4'
],
videoIndex: 0,

baobaoList: [
'static/pchome/baobao-1.png',
'static/pchome/baobao-2.png',
'static/pchome/baobao-3.png',
'static/pchome/baobao-4.png'

],
baobaoIndex: 0,

showVideoBox: false,
videoSrc: '',

bannerStyle: {
height: 'auto'
},

bannerPopupStatus: false,
banner: '',

yiyuanUrl: 'https://h5.m.taobao.com/awp/core/detail.htm?id=576977040134',

scrollTopPx: 0,

showQrCode: false,

home6List: [
{src: 'static/pchome/home-6-img-1.png'},
{src: 'static/pchome/home-6-img-2.png'},
{src: 'static/pchome/home-6-img-3.png'},
{src: 'static/pchome/home-6-img-4.png'},
{src: 'static/pchome/home-6-img-5.png'}
]
}
},
mounted () {
if (new Date('2018-10-08').getTime() - new Date().getTime() > 0) {

}
this.firstVisibility = true















},
methods: {
onBannerClick (index) {
this.scrollTopPx = $(window).scrollTop()
$('#app').css({
height: '1px',
'overflow-y': 'hidden'
})
if (index === 3) {

} else if (index === 0) {

this.banner = 'banner1'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
} else if (index === 1) {
this.banner = 'banner3'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
} else if (index === 2) {
this.banner = 'banner2'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
}
},
playVideo (mp4) {
this.videoSrc = `static/pchome/${mp4}`
this.showVideoBox = true
setTimeout(() => {
$('#video-1')[0].currentTime = 0.5
if (isAndroid === 1) {
$('#video-1')[0].play()
} else {
setTimeout(() => {
$('#video-1')[0].play()
}, 500)
}
}, 300)
},
closeVideo () {
$('#video-1')[0].pause()
this.showVideoBox = false
},
scrollTop () {
$(window).scrollTop(0)
},
goYiyuan () {
if (window.is_wx()) {
let a = new window.ClipboardJS('.yiyuan')
console.log(a)
this.$vux.toast.text(`<span style="font-size: 30px;">链接已复制，请在其他浏览器中打开</span>`)
} else {
window.location.href = this.yiyuanUrl
}
},
goUrl (url, isTaobaoLink = false) {
window.location.href = url
},
onBannerShow () {
this.$nextTick(() => {
this.$refs.bannerPopup.$el.scrollTop = 0
})
},
onBannerHide () {
$('#app').css({
height: 'auto',
'overflow-y': 'auto'
})
$(window).scrollTop(this.scrollTopPx)
},
onWeixinClick () {
this.showQrCode = true
},
onWeiboClick () {
window.location.href = 'https://weibo.com/6087233450/profile?rightmod=1&wvr=6&mod=personinfo'
}
}
}

window.videoUpdate = function (event) {
let miao = 100
if (event.src.includes('zbyrsp')) {
miao = 22
} else if (event.src.includes('hxsp')) {
miao = 36
} else if (event.src.includes('video-kunao')) {
miao = 49
} else if (event.src.includes('video-xizao')) {
miao = 37
} else if (event.src.includes('video-zhuomicang')) {
miao = 21
}
if (event.currentTime >= miao) {
this.showVideoBox = false
}
}



