import Vue from 'vue'
import Router from 'vue-router'

import pcHome from '../components/pc-home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: pcHome,
      meta: {index: 0}
    }
  ]
})



